import { Theme, ChangeType } from './actionTypes';
import { ThemeInterface } from '../../services/themeService';
import { SearchConditionInterface } from '../../services/commonTypes';

export const actionTypes = {
  condition: Symbol('condition'),
  themeData: Symbol('themeData'),
  themeDelete: Symbol('themeDelete'),
  isLodaing: Symbol('isLoading'),
  fetchTheme: Symbol('fetchTheme'),
  deleteTheme: Symbol('deleteTheme'),
  add: Symbol('add'),
  addTheme: Symbol('addTheme'),
  changeTheme: Symbol('changeTheme'),
  asyncChangeTheme: Symbol('asyncChangeTheme'),
};

// 触发条件改变的action
export type SetContitionType = Theme<SearchConditionInterface>;

export function setContitionAction(
  condition: SearchConditionInterface
): SetContitionType {
  return {
    type: actionTypes.condition,
    payload: condition,
  };
}
// 触发删除的action
export type ThemeDeleteType = Theme<string>;

export function deleteAction(id: string): ThemeDeleteType {
  return {
    type: actionTypes.themeDelete,
    payload: id,
  };
}

// 触发加载状态改变的action
export type SetLoadingType = Theme<boolean>;

export function setLoadingAction(bool: boolean): SetLoadingType {
  return {
    type: actionTypes.isLodaing,
    payload: bool,
  };
}

// 触发添加的action
export type AddThemeType = Theme<ThemeInterface>;

export function addAction(theme: ThemeInterface): AddThemeType {
  return {
    type: actionTypes.add,
    payload: theme,
  };
}

// 保存数据和总数的action
export type SaveThemeType = Theme<{
  themes: ThemeInterface[];
  total: number;
}>;

export function saveThemesAction(
  themes: ThemeInterface[],
  total: number
): SaveThemeType {
  return {
    type: actionTypes.themeData,
    payload: {
      themes,
      total,
    },
  };
}

// 改变学习主题的内容等
export type changeThemeType = Theme<{
  type: ChangeType;
  newVal: any;
  id: string;
}>;

export function changeThemeAction(
  type: ChangeType,
  newVal: any,
  id: string
): changeThemeType {
  return {
    type: actionTypes.changeTheme,
    payload: {
      type,
      newVal,
      id,
    },
  };
}

// 触发异步得到数据的action
export function fetchThemeAction() {
  return {
    type: actionTypes.fetchTheme,
  };
}

// 触发异步添加数据的action
export function addThemeAction() {
  return {
    type: actionTypes.addTheme,
  };
}

// 触发异步删除数据的action
export function deleteThemes() {
  return {
    type: actionTypes.deleteTheme,
  };
}

export function asyncChangeSwitch() {
  return {
    type: actionTypes.asyncChangeTheme,
  };
}

export type ThemeActions =
  | SetContitionType
  | ThemeDeleteType
  | SetLoadingType
  | AddThemeType
  | SaveThemeType;
