import { ThemeInterface } from '../../services/themeService';
import { SearchConditionInterface } from '../../services/commonTypes';
import {
  actionTypes,
  SaveThemeType,
  SetContitionType,
  SetLoadingType,
  AddThemeType,
  ThemeDeleteType,
  changeThemeType,
} from '../actions/ThemeAction';
import { ChangeType } from '../actions/actionTypes';

// 描述学习主题的状态类型
export interface ThemeState {
  data: ThemeInterface[]; // 详细数据
  condition: Required<SearchConditionInterface>; // 查询条件
  total: number; // 总数据量
  isLoading: boolean; // 是否正在加载数据
  totalPage: number; // 总页数,
  oneTheme: object;
  id?: string;
  type: ChangeType;
  // specialContent: ChangeType;
  // difficulty: ChangeType;
}

const defaultstate: ThemeState = {
  id: '',
  data: [],
  condition: {
    page: 1,
    limit: 12,
    key: '',
    department: '',
    type: '',
    sharePerson: '',
    specialContent: '',
    difficulty: '',
  },
  total: 0,
  isLoading: false,
  totalPage: 0,
  oneTheme: {},
  type: ChangeType.department,
  // specialContent: ChangeType.specialContent,
  // difficulty: ChangeType.difficulty,
};

function saveTheme(state: ThemeState, action: SaveThemeType): ThemeState {
  return {
    ...state,
    data: action.payload.themes,
    total: action.payload.total,
    totalPage: Math.ceil(action.payload.total / state.condition.limit),
  };
}

function setCondition(state: ThemeState, action: SetContitionType): ThemeState {
  const newState = {
    ...state,
    condition: {
      ...state.condition,
      ...action.payload,
    },
  };
  newState.totalPage = Math.ceil(newState.total / newState.condition.limit);
  return newState;
}

function setLoading(state: ThemeState, action: SetLoadingType): ThemeState {
  return {
    ...state,
    isLoading: action.payload,
  };
}

function addTheme(state: ThemeState, action: AddThemeType): ThemeState {
  return {
    ...state,
    data: [...state.data, action.payload],
    total: state.total + 1,
    oneTheme: action.payload,
  };
}

function deleteTheme(state: ThemeState, action: ThemeDeleteType): ThemeState {
  return {
    ...state,
    id: action.payload,
    data: state.data.filter((m) => m._id !== action.payload),
    total: state.total - 1,
    totalPage: Math.ceil((state.total - 1) / state.condition.limit),
  };
}

function changeSwitch(state: ThemeState, action: changeThemeType) {
  // 1、根据id找到对象
  const theme = state.data.find((d) => d._id === action.payload.id);
  if (!theme) {
    return state;
  }
  // 2、对象克隆
  const newTheme: any = { ...theme };
  newTheme[action.payload.type] = action.payload.newVal;
  // 3、将对象重新放入数组
  const newData = state.data.map((d) => {
    if (d._id === action.payload.id) {
      return newTheme;
    } else {
      return d;
    }
  });

  return {
    ...state,
    data: newData,
    id: action.payload.id,
    type: action.payload.type,
  };
}

export default function themeReducer(
  state: ThemeState = defaultstate,
  action: any
): ThemeState {
  switch (action.type) {
    case actionTypes.themeData:
      return saveTheme(state, action);
    case actionTypes.condition:
      return setCondition(state, action);
    case actionTypes.isLodaing:
      return setLoading(state, action);
    case actionTypes.add:
      return addTheme(state, action);
    case actionTypes.themeDelete:
      return deleteTheme(state, action);
    case actionTypes.changeTheme:
      return changeSwitch(state, action);
    default:
      return state;
  }
}
