import { Dispatch } from 'react';
import { ThemeState } from '../../redux/reducers/themeReducer';
import {
  fetchThemeAction,
  setContitionAction,
  changeThemeAction,
  asyncChangeSwitch,
} from '../../redux/actions/ThemeAction';
import { connect } from 'react-redux';
import RecommendThemes, {
  FirstPageEvents,
} from '../../components/themeTable/recommendThemes';

export interface RootState {
  themeReducer: ThemeState;
}

function mapStateToProps(state: RootState): ThemeState {
  return state.themeReducer;
}

function mapDispatchToProps(dispatch: Dispatch<any>): FirstPageEvents {
  return {
    onLoad() {
      dispatch(fetchThemeAction());
    },
    onSwitchChange(type, newState, id) {
      dispatch(changeThemeAction(type, newState, id));
      dispatch(asyncChangeSwitch());
    },
    onKeyChange(key) {
      console.log(key);
      dispatch(
        setContitionAction({
          key,
        })
      );
    },
    onSearch() {
      dispatch(
        setContitionAction({
          page: 1,
        })
      );
      dispatch(fetchThemeAction());
    },
    onChange(newPage: number) {
      dispatch(
        setContitionAction({
          page: newPage,
        })
      );
      dispatch(fetchThemeAction());
    },
  };
}
export const RecommendThemeList = connect(
  mapStateToProps,
  mapDispatchToProps
)(RecommendThemes);
