import { takeEvery, select, call, put } from 'redux-saga/effects';
import {
  setLoadingAction,
  saveThemesAction,
  actionTypes,
} from '../actions/ThemeAction';
import { ThemeService, ThemeInterface } from '../../services/themeService';

function* fetchThemes() {
  yield put(setLoadingAction(true));
  const condition = yield select((state) => state.themeReducer.condition);
  const resp = yield call(ThemeService.getThemes, condition);
  console.log(resp.total);
  yield put(saveThemesAction(resp.data, resp.total));
  yield put(setLoadingAction(false));
}

function* addThemes() {
  const condition = yield select((state) => state.themeReducer.oneTheme);
  console.log(condition);
  const resp = yield call(ThemeService.add, condition);
  console.log(resp);
}

function* deleteThemes() {
  yield put(setLoadingAction(true));
  const id = yield select((state) => state.themeReducer.id);
  yield call(ThemeService.delete, id);
  yield put(setLoadingAction(false));
}

function* changeTheme() {
  const resp = yield select((state) => state.themeReducer);
  const [data] = resp.data.filter(
    (item: ThemeInterface) => item._id === resp.id
  );
  yield call(ThemeService.edit, data._id, {
    ...data,
    [resp.type]: data[resp.type],
  });
}

export default function* () {
  yield takeEvery(actionTypes.fetchTheme, fetchThemes);
  yield takeEvery(actionTypes.addTheme, addThemes);
  yield takeEvery(actionTypes.deleteTheme, deleteThemes);
  yield takeEvery(actionTypes.asyncChangeTheme, changeTheme);
}
