import axios from 'axios';
import {
  ResponseDataInterface,
  ResponseErrorInterface,
  SearchConditionInterface,
  ResponsePageDataInterface,
} from './commonTypes';

export interface ThemeInterface {
  department: String;
  project: String;
  time: String;
  sharePerson: String;
  description: String;
  number: Number;
  learnWay: String;
  fileType: String;
  adviceDepartment: [String];
  monitor: String;
  helper: String;
  bp: String;
  type: String;
  _id?: string;
  link: string;
  visitor: number;
  timing: number;
  specialContent: string;
  difficulty: string;
}

export class ThemeService {
  public static async add(
    theme: ThemeInterface
  ): Promise<ResponseDataInterface<ThemeInterface> | ResponseErrorInterface> {
    const resp = await axios.post('/api/theme', theme);
    return resp.data;
  }

  public static async edit(
    id: string,
    theme: Partial<ThemeInterface>
  ): Promise<ResponseDataInterface<true> | ResponseErrorInterface> {
    const resp = await axios.put('/api/theme/' + id, theme);
    return resp.data;
  }

  public static async delete(
    id: string
  ): Promise<ResponseDataInterface<true> | ResponseErrorInterface> {
    const resp = await axios.delete('/api/theme/' + id);
    return resp.data;
  }

  public static async getThemeById(
    id: string
  ): Promise<ResponseDataInterface<ThemeInterface | null>> {
    const resp = await axios.get('/api/theme/' + id);
    return resp.data;
  }

  public static async getThemes(
    condition: SearchConditionInterface
  ): Promise<ResponsePageDataInterface<ThemeInterface>> {
    const resp = await axios.get('/api/theme', {
      params: condition,
    });
    return resp.data;
  }
}
