import { Dispatch } from 'react';
import { ThemeState } from '../../redux/reducers/themeReducer';
import {
  fetchThemeAction,
  setContitionAction,
  changeThemeAction,
  asyncChangeSwitch,
} from '../../redux/actions/ThemeAction';
import { connect } from 'react-redux';
import AllThemes, {
  TypePageEvents,
} from '../../components/themeTable/allThemes';
import Test from '../../components/test/test';

export interface RootState {
  themeReducer: ThemeState;
}

function mapStateToProps(state: RootState): ThemeState {
  return state.themeReducer;
}

function mapDispatchToProps(dispatch: Dispatch<any>): TypePageEvents {
  return {
    onLoad() {
      dispatch(fetchThemeAction());
    },
    onSwitchChange(type, newState, id) {
      dispatch(changeThemeAction(type, newState, id));
      dispatch(asyncChangeSwitch());
    },
    onKeyChange(key) {
      console.log(key);
      dispatch(
        setContitionAction({
          key,
        })
      );
    },
    onSearch() {
      dispatch(
        setContitionAction({
          page: 1,
        })
      );
      dispatch(fetchThemeAction());
    },
    onChange(newCondition: any, type: any) {
      console.log(newCondition, type);
      dispatch(
        setContitionAction({
          page: 1,
        })
      );
      dispatch(
        setContitionAction({
          [type]: newCondition,
        })
      );
      dispatch(fetchThemeAction());
    },
  };
}
export const AllThemeList = connect(
  mapStateToProps,
  mapDispatchToProps
)(AllThemes);
export const TestCase = connect(mapStateToProps, mapDispatchToProps)(Test);
