import React from 'react';
import { Col } from 'antd';
import { ChangeType } from '../../redux/actions/actionTypes';
import { ThemeInterface } from '../../services/themeService';
import './themeTable.css';
import background1 from '../../assets/background1.jpg';
import background2 from '../../assets/background2.jpg';
import background3 from '../../assets/background3.jpg';
import background4 from '../../assets/background4.jpg';
import background5 from '../../assets/background5.jpg';
import background6 from '../../assets/background6.jpg';
import background7 from '../../assets/background7.jpeg';
import background8 from '../../assets/background8.jpeg';

const backgroundList = [
  background1,
  background2,
  background3,
  background4,
  background5,
  background6,
  background7,
  background8,
];

interface SingCardProp {
  id: number;
  data: ThemeInterface;
  onSwitchChange: (type: ChangeType, newState: any, id: string) => void;
}

export default function ThemeCard(props: SingCardProp) {
  return (
    <>
      <Col className="gutter-row" span={6}>
        <a
          href={props.data.link}
          target="_blank"
          rel="noopener noreferrer"
          className="theme-card"
        >
          <div
            className="card"
            onClick={() => {
              props.onSwitchChange(
                ChangeType.visitor,
                props.data.visitor + 1,
                props.data._id!
              );
            }}
          >
            <img
              className="background"
              src={backgroundList[props.id % 8]}
              alt="背景图片"
            />
            <div className="pictureText">{props.data.project}</div>
            <div className="card-title">主题：{props.data.project}</div>
            <div className="text">分享人：{props.data.sharePerson}</div>
            <div className="text">点击量：{props.data.visitor}</div>
          </div>
        </a>
      </Col>
    </>
  );
}
